export default {

	data() {
		return {
			categoryPath: '/pages/category/categoryDetail',
			goodsPath:'/pages/product/product',
			slideshowPath:'/pages/test/test',// 轮播图暂无详情页面
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			brand: new Object(),
			slideshowFolderName: "advertisingPicture",
			categoryFolderName: "category",
			goodsFolderName: "goods",
			brandFolderName: "brand",
			categoryList: [],
		};
	},

	onLoad() {
		this.getCarousel();
		this.getCategoryList();
		this.getGoodsList();
		this.getHotestBrand();
	},
	onShow() {
		let _this = this;
		let isLogin = _this.global.isLogin(_this);
		if (!isLogin) {
			return;
		}
	},
	methods: {
		// 轮播图切换序号
		swiperChange(e) {
			this.swiperCurrent = e.detail.current;
		},






		/**
		 * 首页轮播图
		 */
		getCarousel() {
			let _this = this;
			_this.$minApi.uniapp_post(_this.$base_api.queryCarousel, {
				'mallId': _this.$base_api.MALL_ID
			}).then(res => {
				if (res.success) {
					_this.carouselList = res.data;
					_this.swiperLength = res.data.length;
					_this.carouselList.forEach(item => {
						item.src = this.$base_api.fileReview + '?mallId=' + this.$base_api.MALL_ID + '&folderName=' +
							this.slideshowFolderName + '&fileName=' + item.advertisingUrl;
					});
					console.log("轮播图",_this.carouselList)
				} else {
					console.log("查询失败")
				}
			});
		},
		/**
		 * 首页分类
		 */
		getCategoryList() {
			let _this = this;
			_this.$minApi.uniapp_post(_this.$base_api.queryHotterCategoryList, {
				'mallId': _this.$base_api.MALL_ID
			}).then(res => {
				if (res.success) {
					_this.categoryList = res.data;
					_this.categoryList.forEach(item => {
						item.iconUrl = this.$base_api.fileReview + '?mallId=' + this.$base_api.MALL_ID + '&folderName=' +
							this.categoryFolderName + '&fileName=' + item.iconUrl;
					});
				} else {
					console.log("查询失败")
				}
			});
		},
		/**
		 * 首页商品
		 */
		getGoodsList() {
			let _self = this;
			_self.$minApi.uniapp_post(_self.$base_api.queryHotterGoodsList, {
				'mallId': _self.$base_api.MALL_ID
			}).then(res => {
				if (res.success) {
					_self.goodsList = res.data;
					_self.goodsList.forEach(item => {
						item.primaryPicUrl = _self.$base_api.fileReview + '?mallId=' + _self.$base_api.MALL_ID + '&folderName=' +
							_self.goodsFolderName + '&fileName=' + item.primaryPicUrl;
					});
				} else {
					console.log("查询失败")
				}
			});
		},
		/**
		 * 首页最热品牌
		 */
		getHotestBrand() {
			let _this = this;
			_this.$minApi.uniapp_post(_this.$base_api.queryHotestBrand, {'mallId': _this.$base_api.MALL_ID
			}).then(res => {
				if (res.success) {
					_this.brand = res.data;
				} else {
					console.log("查询失败")
				}
			});
		},
		navToCategory(path, item) {
			console.log("item",item)
			uni.navigateTo({
				url: path + '?item=' + encodeURIComponent(JSON.stringify(item))
			})
		},
		toSearchPage(){
			uni.navigateTo({
				url:'/pages/search/search'
			})
		},
		/**
		 * 轮播图跳转
		 */
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			// let goodsId = '8accec5779ea40a8ac377ad8e41c7f80';
			// uni.navigateTo({
			// 	url: `/pages/product/product?id=${goodsId}`
			// })
			console.log("轮播图 title")
		}
	},
}
